<div class="container" :class="{ 'bg-alert': state.isP1 &amp;&amp; state.disparityHP &gt;= 5, 'container-border': config.border }" :style="{transform: `scale(${config.scale})`}"><div class="hp-line" v-if="state.mainBoss.value &gt; 0"><div class="hp-name" :class="{ 'hp-name-light': state.subBoss.value &gt; 0 &amp;&amp; state.mainBoss.value &lt; state.subBoss.value }">{{state.mainBoss.name}}</div><div class="hp-percent">{{state.mainBoss.value}}%</div></div><div class="hp-line" v-if="state.subBoss.value &gt; 0"><div class="hp-name" :class="{ 'hp-name-light': state.mainBoss.value &gt; 0 &amp;&amp; state.mainBoss.value &gt; state.subBoss.value }">{{state.subBoss.name}}</div><div class="hp-percent">{{state.subBoss.value}}%</div></div><div class="hp-line" v-if="state.mainBoss.value &gt; 0 &amp;&amp; state.subBoss.value &gt; 0"><div class="hp-name"></div><div class="hp-percent hp-disparity">{{state.disparityHP}}%</div></div></div>